body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tile-title {
    color: white;
    opacity: .7;
    font-weight: 700;
    position: absolute;
    bottom: 30px;
    left: 40px;
    -webkit-transition: bottom 0.2s, left 0.2s, opacity 0.2s;
    transition: bottom 0.2s, left 0.2s, opacity 0.2s;
    font-size: 1em;
    line-height: 1em;
}

.is-hovered .tile-title {
    bottom: 34px;
    left: 44px;
    opacity: .8;
}

.name {
    grid-area: name;
    background-color: #292F36;
}

.name .tile-title {
    bottom: 36px;
    left: 40px;
    max-width: 260px;
    opacity: .9;
    font-size: 1.2em;
    line-height: .9em;
}

.name .tile-title div:nth-child(1) {
    margin-bottom: 5px;
}

.name .tile-title div:nth-child(2) {
    font-size: .5em;
    line-height: 1.2em;
    margin-top: 5px;
}

.name.is-hovered .tile-title {
    left: 40px;
    bottom: 36px;
}

.about {
    grid-area: about;
    background-color: #058ED9;
}

.work {
    grid-area: work;
    background-color: #FF6B6B;
}

.tech {
    grid-area: tech;
    background-color: #FFE66D;
}

.contact {
    grid-area: contact;
    background-color: lightslategray;
}

.resume {
    grid-area: resume;
    background-color: #087F8C;
}

.resume .tile-title {
    left: 30px;
    font-size: .8em;
}

.resume.is-hovered .tile-title {
    left: 34px;
}



@media (max-width: 1200px) {
    .resume .tile-title {
        left: -8px;
        bottom: 64px;
        -webkit-transform: rotate(270deg);
        transform: rotate(270deg);
    }

    .resume.is-hovered .tile-title {
        left: -4px;
        bottom: 68px;
    }
}

@media (max-width: 800px) {
    .tile-title {
        font-size: .8em;
        bottom: 25px;
        left: 30px;
    }

    .is-hovered .tile-title {
        bottom: 29px;
        left: 34px;
    }

    .name .tile-title div:nth-child(1) {
        margin-bottom: 5px;
    }

    .name .tile-title div:nth-child(2) {
        margin-top: 5px;
    }

    .resume .tile-title {
        left: -13px;
        bottom: 60px;
    }

    .resume.is-hovered .tile-title {
        left: -9px;
        bottom: 64px;
    }
}

@media (max-width: 650px) {
    .tile-title {
        position: static;
        margin: 0 30px 0;
    }

    .name .tile-title {
        max-width: 255px;
        margin: 0 auto;
    }

    .resume .tile-title {
        -webkit-transform: none;
                transform: none;
    }
}

.page-wrapper {
    height: 100vh;
    min-height: 620px;
    max-height: 100%;
    width: 100vw;
    min-width: 405px;
    max-width: 100%;
    justify-content: center;
    text-align: center;
    background-color: #F7FFF7;
}

.grid-container {
    display: grid;
    grid-template-areas:
            'name name name work work work'
            'about about about work work work'
            'tech tech contact contact contact resume';
    grid-gap: 40px;
    height: 91%;
    width: 93%;
    margin: 0 auto;
    padding: 3% 0;
}
.grid-container > div {
    text-align: center;
    font-size: 30px;
    position: relative;
    box-shadow: 2px 1px 1px lightgrey;
    cursor: pointer;
}

.grid-container > div:before {
     content: '';
     position: absolute;
     border: white solid 12px;
     top: 5px;
     left: 5px;
     right: 5px;
     bottom: 5px;
     opacity: .4;
    -webkit-transition-duration: 0.2s;
    transition-duration: 0.2s;
    -webkit-transition-property: border;
    transition-property: border;
 }

.grid-container > div:hover:before {
    border: white solid 16px;
    opacity: .9;
}

.grid-container > div:first-child {
    text-align: left;
    cursor: default;
    opacity: .9;
}

.grid-container > div:first-child:before  {
    border: white solid 16px;
    opacity: .9;
    -webkit-transition-property: none;
    transition-property: none;
    top: 7px;
    left: 7px;
    right: 7px;
    bottom: 7px;
}

.grid-container > div:first-child:hover:before  {
    border: white solid 16px;
}

@media (max-width: 800px) {
    .grid-container {
        grid-template-areas:
                'name name name name name name'
                'about about work work work work'
                'tech tech tech contact contact resume';
        grid-gap: 30px;
        height: 93%;
        width: 95%;
        margin: 0 auto;
        padding: 3% 0;
    }
}

@media (max-width: 650px) {
    .grid-container {
        grid-template-areas:
                'name'
                'name'
                'about'
                'work'
                'tech'
                'contact'
                'resume';
        grid-gap: 10px;
        height: 96%;
        width: 96%;
        margin: 0 auto;
        padding: 3% 0;
    }

    .grid-container > div {
        display: flex;
        align-items: center;
    }

    .grid-container > div:before {
        border: white solid 10px;
    }

    .grid-container > div:hover:before {
        border: white solid 14px;
    }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

